<template>
  <div id="follow">
    <van-pull-refresh v-model="$store.state.follow.refreshing" @refresh="onRefresh">
      <van-list
          v-model="$store.state.follow.loading"
          :finished="$store.state.follow.finished"
          finished-text="没有更多了"
          @load="onLoad"
      >
        <van-cell v-for="(item,index) in list" :key="index">
          <router-link :to='{name:"detail",params:{id:item._id},query:{id:item._id,toPath:"follow"}}'>
            <h2>{{ index + 1 }}.{{ item.title }}</h2>
            <p>{{ item.des }}</p>
          </router-link>
        </van-cell>
      </van-list>
    </van-pull-refresh>

  </div>
</template>

<script>
import {mapState,mapActions} from "vuex"
import {throttle} from 'lodash/function'

export default {
  name: "follow",
  data(){
    return {

    }
  },
  methods: {
    ...mapActions('follow',['follow']),
    onLoad:throttle(function(){
      this.follow({_page: parseInt(this.list.length / 14)})
    },600),
    onRefresh() {
      this.onLoad();
    },
  },
  computed:mapState({
      list:state=>state.follow.list
  })
}
</script>

